import React from "react";
import { NavLink, Link } from "react-router-dom";
import { Nav } from "reactstrap";
// javascript plugin used to create scrollbars on windows
import PerfectScrollbar from "perfect-scrollbar";

import logo from "logo.svg";

var ps;

class Sidebar extends React.Component {
  constructor(props) {
    super(props);
    this.activeRoute.bind(this);
    this.sidebar = React.createRef();
  }
  // verifies if routeName is the one active (in browser input)
  activeRoute(routeName) {
    return this.props.location.pathname.indexOf(routeName) > -1 ? "active" : "";
  }
  componentDidMount() {
    if (navigator.platform.indexOf("Win") > -1) {
      ps = new PerfectScrollbar(this.sidebar.current, {
        suppressScrollX: true,
        suppressScrollY: false,
      });
    }
  }
  componentWillUnmount() {
    if (navigator.platform.indexOf("Win") > -1) {
      ps.destroy();
    }
  }
  render() {
    return (
      <div
        className="sidebar"
        data-color={this.props.bgColor}
        data-active-color={this.props.activeColor}
      >
        <div className="logo">
          <a
            href="https://www.creative-tim.com"
            className="simple-text logo-mini"
          >
            <div className="logo-img">
              <img src={logo} alt="react-logo" />
            </div>
          </a>
          <a
            href="https://www.creative-tim.com"
            className="simple-text logo-normal"
          >
            Creative Tim
          </a>
        </div>
        <div className="sidebar-wrapper" ref={this.sidebar}>
          <Nav>
            {this.props.routes
              .filter((route) => !route.hidden)
              .map((prop, key) => {
                return (
                  <li
                    className={
                      this.activeRoute(prop.path) +
                      (prop.pro ? " active-pro" : "")
                    }
                    key={key}
                  >
                    <NavLink
                      to={prop.layout + prop.path}
                      className="nav-link"
                      activeClassName="active"
                    >
                      <i className={prop.icon} />
                      <p>{prop.name}</p>
                    </NavLink>
                  </li>
                );
              })}
            <div className="logo">
              <a
                href="https://www.creative-tim.com"
                className="simple-text logo-mini"
              >
                <div className="logo-img">
                  <img src={logo} alt="react-logo" />
                </div>
              </a>
              <a
                href="https://nguyentuxuancong.github.io/?fbclid=IwAR2Iq6YSkAYA8Po8eRZQ8e-Yp47t3ITqDe7WK6DpopAHqRoLwpr4_dbbfRQ"
                className="simple-text logo-normal"
              >
                Chức năng nhóm 1
              </a>
            </div>

            <div className="logo">
              <a
                href="https://www.creative-tim.com"
                className="simple-text logo-mini"
              >
                <div className="logo-img">
                  <img src={logo} alt="react-logo" />
                </div>
              </a>
              <a href="http://bibiweb.xyz" className="simple-text logo-normal">
                Quản lý trạm điện và đợt kiểm tra
              </a>
            </div>
            <div className="logo">
              <a
                href="https://www.creative-tim.com"
                className="simple-text logo-mini"
              >
                <div className="logo-img">
                  <img src={logo} alt="react-logo" />
                </div>
              </a>
              <a
                href="https://angular-project-51bf8.web.app/login"
                className="simple-text logo-normal"
              >
                Quản trị lưới điện
              </a>
            </div>
            <div className="logo">
              <a
                href="https://www.creative-tim.com"
                className="simple-text logo-mini"
              >
                <div className="logo-img">
                  <img src={logo} alt="react-logo" />
                </div>
              </a>
              <a
                href="http://nhom6.now.sh/"
                className="simple-text logo-normal"
              >
                Quản lí sự cố
              </a>
            </div>
          </Nav>
        </div>
      </div>
    );
  }
}

export default Sidebar;
